/*	=========================================== *\
		BODY
\*	===========================================	*/
body {
  margin: 0px;
  padding: 0px;
  color: #333333;
}
/*	=========================================== *\
		CUSTOMIZE TEXT SELECTION
\*	===========================================	*/
::-moz-selection {
  background: #f7b0b0;
  text-shadow: none;
}
::selection {
  background: #f7b0b0;
  text-shadow: none;
}
/*	=========================================== *\
		RESPONSIVE FLEXIBLE IMAGE ELEMENT
\*	===========================================	*/
img {
  max-width: 100%;
}
a img {
  border: none;
}
/*	=========================================== *\
		PAGE WRAPPER
\*	===========================================	*/
#page-wrapper {
  padding: 48px 0;
  position: relative;
}
#page-wrapper #page-wrapper-bg-img {
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-position: 50% 50%;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  filter: alpha(opacity=50);
  opacity: 0.5;
  z-index: -1;
}
