		GLOBAL REVIEWS AREA
\*	===========================================	*/
#reviews-tier {
  padding: 48px 0;
  text-align: center;
  color: #FFFFFF;
  background-color: rgba(51, 51, 51, 0.4);
  background-blend-mode: darken;
  overflow: hidden;
}
/*	=========================================== *\
		GLOBAL EMAIL SIGNUP AREA
\*	===========================================	*/
#email-signup-tier {
  padding: 48px 0;
  background-color: #FFF;
  border: 4px solid #BD2E2B;
  border-left: 0;
  border-right: 0;
  overflow: hidden;
