  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity .3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  width: 400px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all .3s ease;
}
.modal-header {
  border-bottom: 1px solid darkgrey;
}
.modal-header h4 {
  padding: 10px 10px 0px 10px;
}
.modal-header h4 span {
  float: right;
}
.modal-body {
  padding: 20px;
}
.modal-default-button {
  float: right;
}
.modal-enter,
.modal-leave {
  opacity: 0;
}
.modal-enter .modal-container,
.modal-leave .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
#slides {
  display: none;
}
#slides .slidesjs-navigation {
  margin-top: 3px;
  font-size: 22px;
  font-weight: bold;
}
.slidesjs-stop,
.slidesjs-play {
  display: none !important;
}
#slides .slidesjs-previous {
  margin-right: 5px;
  float: left;
}
#slides .slidesjs-next {
  margin-right: 5px;
  float: left;
}
.slidesjs-pagination {
  margin: 6px 0 0;
  float: right;
  list-style: none;
}
.slidesjs-pagination li {
  float: left;
  margin: 0 1px;
}
.slidesjs-pagination li a {
  display: block;
  width: 13px;
  height: 0;
  padding-top: 13px;
  background-image: url(/assets/images/slides/pagination.png);
  background-position: 0 0;
  float: left;
  overflow: hidden;
}
.slidesjs-pagination li a.active,
.slidesjs-pagination li a:hover.active {
  background-position: 0 -13px;
}
.slidesjs-pagination li a:hover {
  background-position: 0 -26px;
}
#slides a:link,
#slides a:visited {
  color: #333;
}
#slides a:hover,
#slides a:active {
  color: #9e2020;
}
.navbar {
  overflow: hidden;
}
#slides {
  display: none;
}
.container {
  margin: 0 auto;
}
/* For tablets & smart phones */
@media (max-width: 767px) {
  body {
    padding-left: 20px;
    padding-right: 20px;
  }
  .container {
    width: auto;
  }
}
/* For smartphones */
@media (max-width: 480px) {
  .container {
    width: auto;
  }
}
/* For smaller displays like laptops */
@media (min-width: 768px) and (max-width: 979px) {
  .container {
    width: 724px;
  }
}
/* For larger displays */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
.sweet-alert > fieldset {
  display: none;
}
